import React from "react";
import { ParseStringAsHTML } from 'wrappers/Wrappers';

export function ConvertDate(dateString) {
    if (dateString !== undefined) {
        return new Date(dateString).toLocaleString("en-GB", {
            day: "numeric",
            month: "short",
            year: "numeric",
            hour: "numeric",
            minute: "2-digit"
          });
    }
    else {
        return undefined;
    }
}

export function GetAuthorsInitial(author) {
    if (author !== undefined) {
        var authorSplit = author.split(' ');
        return authorSplit[0].charAt(0) + authorSplit[authorSplit.length - 1].charAt(0);
    }
    else {
        return undefined;
    }
}


export function GenerateMetaTags(title, description){
    var list = document.getElementsByTagName("head")[0]; 
    list.insertBefore(createNewMetaTag('og:title', title), list.childNodes[0]);
    list.insertBefore(createNewMetaTag('og:description', description), list.childNodes[0]);
    list.insertBefore(createNewMetaTag('twitter:title', title), list.childNodes[0]);
    list.insertBefore(createNewMetaTag('twitter:description', description), list.childNodes[0]);
    var newItem = document.createElement("title");
    var newItemInnerText = document.createTextNode(title);
    newItem.appendChild(newItemInnerText); 
    list.insertBefore(newItem, list.childNodes[0]);
}

export function createNewMetaTag(key, value){
    var newItem = document.createElement("meta");
    newItem.setAttribute('property', key);
    newItem.setAttribute('content', value);
    return newItem
}

export function TextToClipboard1 (text) {
    var dummy = document.createElement("textarea");
    document.body.appendChild(dummy);
    dummy.value = text;
    dummy.select();
    document.execCommand("copy");
    document.body.removeChild(dummy);
}

export const TextToClipboard = (content) => {
    const el = document.createElement('textarea');
    el.value = content;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
  };


  export const Linkify = (str) => {
    var newStr = str.replace(/(<a href=")?((https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)))(">(.*)<\/a>)?/gi, function () {

        return '<a target="_blank" href="' + arguments[2] + '">' + (arguments[7] || arguments[2]) + '</a>'
    });
    return ParseStringAsHTML(newStr);
}