import React from 'react';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MuiLink from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { AuthenticatedTemplate } from "@azure/msal-react";
import { useMsal, useAccount } from "@azure/msal-react";
import { blogBaseUrl } from 'configs/SiteConfig';
import { BlogDelete } from 'services/BlogService';
import { scopeBipulAppAPI } from 'configs/AuthConfig';
import { TextToClipboard } from 'utils/CommonMethods';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });

export default function BlogMenu(props) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    // Status : 0 = Nothing, 1 = Deleting Started , 2 = Deleting Successful, 3 = Deletion Failed.
    const [deletingStatus, setDeletingStatus] = React.useState(0);
    const { instance, accounts } = useMsal();
    const account = useAccount(accounts[0] || {});
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const openInNewTab = () => {
        setAnchorEl(null);
    };

    const [openConsent, setOpenConsent] = React.useState(false);

    const handleConsentClickOpen = () => {
        setAnchorEl(null);
        setOpenConsent(true);
    };

    const handleConsentClose = () => {
        setOpenConsent(false);
    };

    const handleCopyLink = () => {
        TextToClipboard(blogBaseUrl + props.blogId);
    }

    const handleBlogDeleted = () => {
        props.onBlogDeleted(true);            
    }

    const DeleteBlog = () => {
        setDeletingStatus(1);
        if (account) {
            instance.acquireTokenSilent({
                account: account,
                scopes: scopeBipulAppAPI
            }).then((response) => {
                BlogDelete(response.accessToken, props.blogId).then(response => {
                    if (response === true) {                        
                        setDeletingStatus(2);
                        handleBlogDeleted();
                    }
                    else {
                        setDeletingStatus(3);
                    }
                });
            }).catch((error) => {
                console.log(error);
                setDeletingStatus(3);
            });
        }
    };

    return (
        <div>
            <IconButton
                aria-label="more"
                aria-controls="long-menu"
                aria-haspopup="true"
                onClick={handleClick}
            >
                <MoreVertIcon />
            </IconButton>
            <Menu
                id="long-menu"
                anchorEl={anchorEl}
                keepMounted
                open={open}
                onClose={handleClose}
            >
                {
                    <div>
                        <MuiLink href={blogBaseUrl + props.blogId} target="_blank" underline="none" color="textPrimary" >
                            <MenuItem onClick={openInNewTab}>
                                Open in New Tab
                         </MenuItem>
                        </MuiLink>
                        {/* <MenuItem onClick={handleCopyLink}>
                                Copy Link
                        </MenuItem> */}
                        <AuthenticatedTemplate>
                            <MenuItem onClick={handleConsentClickOpen}>
                                Delete
                        </MenuItem>
                        </AuthenticatedTemplate>

                    </div>
                }
            </Menu>
            <div>
                <Dialog
                    open={openConsent}
                    TransitionComponent={Transition}
                    keepMounted
                    onClose={handleClose}
                    aria-labelledby="consent-title"
                    aria-describedby="consent-description"
                >
                    <DialogTitle id="consent-title">{"Alert : Blog Deletion Consent."}</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="consent-description">
                            { (deletingStatus === 0) &&
                                ("Are you sure that you want to DELETE this blog permanently ?")
                            }
                            { (deletingStatus === 1) &&
                                ("Deleting.. Please Wait !")
                            }
                            { (deletingStatus === 2) &&
                                ("Blog Deleted !")
                            }
                            { (deletingStatus === 3) &&
                                ("Blog Deletion  Failed !")
                            }                       
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        { deletingStatus === 0 ?
                            (<div><Button onClick={handleConsentClose} color="primary">Disagree</Button>
                            <Button onClick={DeleteBlog} color="primary">Agree</Button></div>)
                            : (<div><Button onClick={handleConsentClose} color="primary">Close</Button></div>)
                        }
                    </DialogActions>
                </Dialog>
            </div>
        </div>
    );
}
